$('.exhibitions__slider').slick({
    centerMode: true,
    slidesToShow: 2,
    prevArrow: $('.exhibitions__prev'),
    nextArrow: $('.exhibitions__next'),
    responsive: [
        {
            breakpoint: 1400,
            settings: {
                slidesToShow: 3,
                centerMode: false,
            }
        },
        {
            breakpoint: 1151,
            settings: {
                slidesToShow: 2,
                centerMode: false,
            }
        },
        {
            breakpoint: 360,
            settings: {
                slidesToShow: 1,
                centerMode: false,
            }
        }
    ]
});

$(document).ready(function() {

    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__wrap').toggleClass('header__wrap--active')
        $('body').toggleClass('fixed-body')
    })

})
